.our-products {
  margin-top: 80px;

  &__title {
    margin-bottom: 24px;
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 20px;
    text-align: center;

    @media (max-width: 767px) {
      grid-template-columns: 1fr;
      grid-gap: 48px;
    }
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    overflow: hidden;

    .swiper-container {
      position: static;
    }

    .swiper-button-prev,
    .swiper-button-next {
      top: 117px;
    }

    .swiper-pagination {
      top: 184px;
    }
  }

  &__images-wrapper {
    order: -1;
    width: 100%;
    height: 234px;
    margin-bottom: 20px;
  }

  &__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__subtitle,
  &__description {
    padding: 0 20px;

    @media (max-width: 1024px) {
      padding: 0;
    }
  }

  &__description {
    margin: 0;
  }
}
