.site-footer {
  padding-top: 80px;
  padding-bottom: 48px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;

  &__copyright,
  &__developers-wrapper {
    min-width: 180px;
    line-height: 1em;

    @media (max-width: 767px) {
      min-width: auto;
    }
  }

  &__developers-wrapper {
    display: grid;
    justify-items: end;
  }

  &__developers-text {
    margin-top: 8px;
    color: inherit;
    text-decoration: none;

    &:hover {
      color: #ce3141;
    }
  }

  &__developers-icon,
  &__instagram-link {
    transition: opacity 0.3s;

    &:hover {
      opacity: 0.7;
    }
  }

  &__instagram-link {
    display: block;
    width: 28px;
    height: 28px;
    background-image: url('images/instagram-icon@2x.png');
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;

    @media (max-width: 767px) {
      display: none;
    }
  }
}
