.order-processing {
  margin-top: 80px;

  &__list {
    counter-reset: list;
    margin: 0;
    margin-top: 39px;
    padding: 0;
    list-style: none;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 48px 30px;

    @media (max-width: 1200px) {
      grid-template-columns: repeat(3, 1fr);
    }

    @media (max-width: 660px) {
      grid-template-columns: 1fr;
    }
  }

  &__item {
    position: relative;
    padding-top: 55px;

    @media (max-width: 660px) {
      padding-top: 48px;
      text-align: center;
    }

    &::before {
      counter-increment: list;
      content: counter(list);
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;
      font-family: 'Noto Serif';
      font-size: 36px;
      line-height: 1em;
      font-weight: 400;

      @media (max-width: 660px) {
        left: 50%;
        transform: translateX(-50%);
        font-size: 34px;
      }
    }

    &::after {
      content: '';
      position: absolute;
      background-color: #ffa2a8;

      @media (max-width: 660px) {
        transform: translateX(-50%);
      }
    }

    &:nth-child(1)::after {
      top: -10px;
      left: 0;
      width: 23px;
      height: 33px;

      @media (max-width: 660px) {
        left: calc(50%);
      }
    }

    &:nth-child(2)::after {
      top: -3px;
      left: 6px;
      width: 36px;
      height: 22px;

      @media (max-width: 660px) {
        left: calc(50% + 6px);
      }
    }

    &:nth-child(3)::after {
      top: -2px;
      left: 2px;
      width: 29px;
      height: 22px;

      @media (max-width: 660px) {
        left: calc(50% + 2px);
      }
    }

    &:nth-child(4)::after {
      top: -5px;
      left: 6px;
      width: 22px;
      height: 23px;

      @media (max-width: 660px) {
        left: calc(50% + 6px);
      }
    }

    &:nth-child(5)::after {
      top: 1px;
      left: 9px;
      width: 27px;
      height: 19px;

      @media (max-width: 660px) {
        left: calc(50% + 9px);
      }
    }
  }

  &__step-title {
    max-width: 100px;

    @media (max-width: 1024px) {
      max-width: 100%;
    }
  }

  &__step-description {
    margin: 0;
  }

  &__strong-text {
    font-weight: 400;
  }
}
