.own-production {
  margin-top: 80px;

  &__grid-wrapper {
    margin-top: 36px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 12px;
    align-items: stretch;

    @media (max-width: 540px) {
      display: flex;
      flex-wrap: wrap;
      grid-gap: 0;
    }
  }

  &__grid-item {
    &--large {
      grid-column: 1 / 4;
      grid-row: 1 / 3;
    }

    &--long {
      grid-column: span 2;

      @media (max-width: 540px) {
        order: 3;
      }
    }

    &--desktop {
      @media (max-width: 540px) {
        display: none;
      }
    }

    &--text {
      @media (max-width: 540px) {
        order: 2;
        margin-bottom: 12px;
      }
    }

    &--left-inline,
    &--right-inline {
      @media (max-width: 540px) {
        margin: 12px 0;
        width: 48.2%;
        display: inline-block;
      }
    }

    &--right-inline {
      margin-left: auto;
    }
  }

  &__image {
    display: block;
    object-fit: cover;
    width: 100%;
    height: 100%;
  }

  &__text-block {
    margin: 0;
    padding: 30% 6px 20% 6px;
    text-align: center;
    background-image: url('images/time-icon-pink.svg');
    background-repeat: no-repeat;
    background-position: top 24px center;
    background-size: 18% auto;

    @media (max-width: 768px) {
      padding: 11% 0;
      font-size: 15px;
      background-image: none;
    }

    @media (max-width: 425px) {
      font-size: 21px;
    }

    @media (max-width: 340px) {
      padding: 16px 0;
      font-size: 18px;
    }
  }
}
