.swiper-button-prev,
.swiper-button-next {
  position: relative;
  padding: 0;
  border: none;
  width: 40px !important;
  height: 40px !important;
  background-color: rgba(40, 40, 40, 0.4);
  border-radius: 50%;

  &::after {
    content: none !important;
  }

  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url('images/arrow-icon.svg');
    background-position: center center;
    transition: opacity 0.15s;
  }

  &:hover::before {
    opacity: 0.7;
  }

  &:focus {
    outline: none;

    &::before {
      opacity: 0.7;
    }
  }
}

.swiper-button-prev {
  left: 8px !important;
  transform: rotate(180deg);

  &::before {
    transform: translateY(1px);
  }
}

.swiper-button-next {
  right: 8px !important;
}

.swiper-pagination-fraction {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 40px !important;
  height: 40px;
  left: 50% !important;
  transform: translateX(-50%) !important;
  font-size: 14px;
  line-height: 1em;
  font-weight: 400;
  color: #ffffff;
  background-color: rgba(40, 40, 40, 0.4);
  border-radius: 50%;
}
