.reviews-block {
  margin-top: 80px;
  padding: 68px 96px;
  background-color: #fff2f0;
  background-image: url('images/reviews-bg.svg');
  background-size: cover;

  @media (max-width: 1024px) {
    padding-left: 21px;
    padding-right: 21px;
  }

  @media (max-width: 540px) {
    padding-left: 12px;
    padding-right: 12px;
  }

  &__list {
    margin: 0;
    margin-top: 40px;
    padding: 0;
    list-style: none;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 50px 16px;
    text-align: center;

    @media (max-width: 1024px) {
      grid-gap: 50px 32px;
    }

    @media (max-width: 540px) {
      grid-template-columns: 1fr;
      grid-gap: 40px;
    }
  }

  &__wrapper,
  &__text {
    margin: 0;
  }

  &__wrapper {
    padding-top: 60px;
    background-image: url('images/quote-icon.svg');
    background-size: 46px auto;
    background-repeat: no-repeat;
    background-position: top center;

    @media (max-width: 540px) {
      padding-top: 48px;
      background-size: 42px auto;
    }
  }

  &__text {
    font-family: 'Noto Serif';
    font-weight: 400;
    font-style: italic;

    @media (max-width: 1024px) {
      font-size: 15px;
    }

    @media (max-width: 425px) {
      font-size: 21px;
    }

    @media (max-width: 340px) {
      font-size: 18px;
    }
  }

  &__author {
    display: block;
    margin-top: 8px;
    font-style: normal;
  }
}
