.address-block {
  position: relative;
  margin-top: 160px;
  padding-bottom: 135px;
  display: flex;

  @media (max-width: 1024px) {
    margin-top: 80px;
    padding-bottom: 80px;
  }

  @media (max-width: 767px) {
    padding-bottom: 264px;
  }

  &__contacts-wrapper {
    position: relative;
    z-index: 3;
    margin-top: 0;
    margin-left: auto;
    max-width: 437px;
    font-weight: 400;
    color: #ffffff;
    background-color: #64080d;

    @media (max-width: 767px) {
      max-width: initial;
      width: 100vw;
      margin-left: -21px;
    }

    @media (max-width: 540px) {
      margin-left: -12px;
    }
  }

  &__map-wrapper {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: calc(100% - 80px);
    background-color: #f2f3f4;

    @media (max-width: 767px) {
      height: 264px;
    }
  }
}
