.curtain-examples {
  margin-top: 80px;
  text-align: center;

  &__common-wrapper {
    max-width: 850px;
    margin: 0 auto;

    @media (max-width: 1024px) {
      max-width: 100%;
    }
  }

  &__title {
    margin-bottom: 24px;
  }

  &__top-paragraph {
    margin: 0 auto;
    max-width: 70%;

    @media (max-width: 540px) {
      max-width: 100%;
    }
  }

  &__image {
    display: block;
    margin-bottom: 16px;
    width: 100%;
    height: auto;
  }

  &__columns-wrapper {
    margin-top: 40px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 40px;

    @media (max-width: 540px) {
      grid-template-columns: 1fr;
      grid-gap: 50px;
    }
  }
}
